import React, { useMemo } from 'react'
import { Redirect, useParams } from 'react-router-dom'

import { heroImages } from '../../helpers/heroImages';
import { getHeroById } from '../../selectors/getHeroById';


export const HeroScreen = ({history}) => {
    const {heroeId} = useParams();

    const hero= useMemo(() => getHeroById(heroeId), [heroeId])
    

    if( !hero){
        return <Redirect to="/"/>
    }

    const handleReturn = () => {
        if(history.length <= 2){
            history.push('/');
        }else{
            history.goBack();
        }
    }

    const {
        superhero,
        publisher,
        alter_ego,
        first_appearance,
        characters,
    } = hero;
    return (
        <div className="row mt-5 animate__animated animate__flipInX">
            <div className="col-4">
                <img src={ heroImages(`./${heroeId}.jpg`).default} alt={superhero} className="img-thumbnail"/>
            </div>
            <div className="col-8">
                <h3>{superhero}</h3>
                <ul className="list-group list-group-flush">
                    <li className="list-group-item"><b>Alter ego: </b> {alter_ego} </li>
                    <li className="list-group-item"><b>Publisher: </b> {publisher} </li>
                    <li className="list-group-item"><b>First Appearance: </b> {first_appearance} </li>
                    <li className="list-group-item"><b>Characters: </b> {characters} </li>
                </ul>
                
                <button className="btn btn-outline-info" onClick={handleReturn}> Return </button>
            </div>
        </div>
    )
}
